@all-color: @color-gray, @color-red, @color-pink, @color-grape, @color-violet, @color-indigo, @color-blue, @color-cyan, @color-teal, @color-green, @color-lime, @color-yellow, @color-orange;

.generate-color(@length);

.generate-color(@n, @index: 1) when (@index =< @n) {
    @color: extract(@list, @index);
    .color-class(@color, @index, 9);
    .generate-color(@n, (@index + 1));
}

.color-class(@color, @color-index, @n, @index: 0) when (@index =< @n) {
    @color-name: ~'.md-color-@{color}-@{index}';
    @{color-name} {
        color: extract(extract(@all-color, @color-index), (@index + 1));
    }
    .color-class(@color, @color-index, @n, (@index + 1));
}
